/* Timeline */
.timeline {
  max-width: 80%;
  border-left: 5px solid rgb(3, 132, 89);
  border-bottom-left-radius: 3px;
  border-top-left-radius: 3px;
  color: white;
  letter-spacing: 0.5px;
  position: relative;
  line-height: 1.4em;
  font-size: 1.7rem;
  padding: 50px;
  list-style: none;
  text-align: left;
  font-weight: 100;
  margin: 0;
}
@media (min-width: 768px) {
  .timeline {
    max-width: 70%;
  }
}
@media (min-width: 768px) {
  .timeline {
    padding: 50px;
  }
}
@media (min-width: 768px) {
  .timeline {
    margin: 0 auto;
  }
}
.timeline h2 {
  text-align: left;
  margin: 4rem 1rem;
  text-transform: uppercase;
  font-weight: 800;
  color: #4298c3;
}
.timeline .event {
  border-bottom: 1px dashed fade(white, 10%);
  padding-bottom: 25px;
  margin-bottom: 50px;
  position: relative;
  padding: 0 1rem 0 1rem;
}
.timeline .event .vermas {
  position: relative;
  display: block;
  height: 60px;
  z-index: 999999999;
}
.timeline .event .vermas p:hover {
  cursor: pointer;
  top: 0;
}
.timeline .event h3 {
  margin: 4rem 0;
  font-size: 1.5rem;
}
@media (min-width: 1500px) {
  .timeline .event h3 {
    font-size: 2rem;
    margin: 2rem 0;
  }
}
.timeline .event:last-of-type {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none;
}
.timeline .event:before, .timeline .event:after {
  position: absolute;
  display: block;
  top: 0;
}
.timeline .event:before {
  top: 25px;
  font-size: 0.7em;
  color: fade(white, 40%);
  content: attr(data-date);
  font-weight: 100;
  min-width: 150px;
}
@media (min-width: 1500px) {
  .timeline .event:before {
    top: 0;
    left: -217.5px;
    font-size: 0.9em;
  }
}
@media (min-width: 1500px) {
  .timeline .event:before {
    text-align: right;
  }
}
.timeline .event:after {
  box-shadow: 0 0 0 4px #4298c3;
  left: -57.85px;
  background: #252827;
  border-radius: 50%;
  height: 11px;
  width: 11px;
  content: "";
  top: 5px;
}
.timeline .event .event-text {
  color: white;
  font-size: 1.3rem;
  overflow: hidden;
  max-height: 210px;
  transition: max-height 0.5s ease-in-out;
}
.timeline .event .event-text.desplegado {
  overflow: visible;
  max-height: 800px;
}

