html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  height: 100%;
  overflow-x: hidden;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 510;
}

p, a {
  font-family: "Poppins", sans-serif;
}

.p-translate {
  text-align: center;
  font-size: 15px;
  cursor: pointer;
}

.container-back-index {
  position: relative;
  z-index: 10;
  margin: 2rem 0;
}

.back-index {
  display: inline;
  color: black;
  margin: 3rem 5rem;
  font-weight: 700;
  font-size: 16px;
  animation: latidos 0.8s infinite;
}

@keyframes latidos {
  0% {
    transform: none;
  }
  50% {
    font-size: 15px;
  }
  100% {
    transform: none;
  }
}
/*AOS ANIMATIONS*/
.contenedor-nav {
  padding: 1.2rem 0;
  background: rgb(1, 7, 29);
  background: linear-gradient(270deg, rgb(1, 7, 29) 0%, rgb(0, 1, 10) 100%);
  text-align: center;
}
.contenedor-nav a {
  color: white;
  font-size: 2rem;
}

.hover-underline-animation {
  display: inline-block;
  position: relative;
  color: #0087ca;
}

.hover-underline-animation::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #12f6df;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.hover-underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.hover-underline-animation:hover {
  font-size: 2.1rem;
  transition: font-size 0.5s ease-out;
}

.gulp {
  width: 100px;
}